// import sinon from "sinon";

import Button from "../src/button";

const expect = chai.expect;
import Vue from 'vue'
import Input from '../src/input'
import sinon from "sinon";

Vue.config.productionTip = false
Vue.config.devtools = false

describe('Input', () => {
    it('.', () => {
        expect(Input).to.be.ok
    })

    describe('props',()=>{
        const Constructor = Vue.extend(Input)  //
        let vm

        // 
        beforeEach(function (){

        });

        //
        afterEach(function (){
            vm.$destroy()  //
        })

        it(' value',()=>{
             vm = new Constructor({
                propsData: {
                    value: '1234'
                }
            }).$mount()
            const inputElement = vm.$el.querySelector('input')
            expect(inputElement.value).to.equal('1234')
        })

        it(' disabled',()=>{
             vm = new Constructor({
                propsData: {
                    disabled: true
                }
            }).$mount()
            const inputElement = vm.$el.querySelector('input')
            expect(inputElement.disabled).to.equal(true)
        })

        it(' readonly',()=>{
             vm = new Constructor({
                propsData: {
                    readonly: true
                }
            }).$mount()
            const inputElement = vm.$el.querySelector('input')
            expect(inputElement.readOnly).to.equal(true)
        })

        it(' readonly',()=>{
             vm = new Constructor({
                propsData: {
                    error: ''
                }
            }).$mount()
            const useElement = vm.$el.querySelector('use')
            expect(useElement.getAttribute('xlink:href')).to.equal('#iinfo')
            const inputElement = vm.$el.querySelector('span')
            expect(inputElement.innerText).to.equal('')
        })
    })

    describe('',()=>{
        const Constructor = Vue.extend(Input)  //
        let vm

        //
        afterEach(function (){
            vm.$destroy()  //
        })

        it(' change / input / focus / blur ',()=>{
            ['change' , 'input' , 'focus' , 'blur'].forEach((item) => {
                vm = new Constructor({}).$mount()
                const callback = sinon.fake();
                vm.$on(item,callback)
                //inputchange 
                let event = new Event(item)
                Object.defineProperty(
                    event,'target',{
                      value: {value: 'hi'}, enumerable: true
                    }
                )
                let inputEvent = vm.$el.querySelector('input')
                inputEvent.dispatchEvent(event)
                expect(callback).to.have.been.calledWith('hi')  //changechangeevent
            })
        })
    })
})